<template>
  <div class="herosCreate">
    <h1>{{ id ? '' : '' }}</h1>
    <el-form :model="herosCreateForm"
             ref="herosCreateFormRef"
             label-width="100px"
             :rules="herosCreateFormRules">
      <!--  -->
      <el-tabs value="basic"
               type="border-card">
        <!--  -->
        <el-tab-pane label=""
                     name="basic">
          <!--  -->
          <el-form-item label=""
                        prop="name">
            <el-input type="name"
                      v-model="herosCreateForm.name"
                      autocomplete="off"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="avatar">
            <!--  -->
            <el-upload class="avatar-uploader"
                       :action="uploadUrl"
                       :headers="setHeaders()"
                       :show-file-list="false"
                       :on-success="uploadSuccess">
              <img v-if="herosCreateForm.avatar"
                   :src="herosCreateForm.avatar"
                   class="avatar" />
              <i v-else
                 class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="banner">
            <!--  -->
            <el-upload class="avatar-uploader"
                       :action="uploadUrl"
                       :headers="setHeaders()"
                       :show-file-list="false"
                       :on-success="res => $set(herosCreateForm,  'banner', res.icon )">
              <img v-if="herosCreateForm.banner"
                   :src="herosCreateForm.banner"
                   class="avatar" />
              <i v-else
                 class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="title">
            <el-input v-model="herosCreateForm.title"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="categories">
            <el-select v-model="herosCreateForm.categories"
                       multiple>
              <el-option v-for="item in categories"
                         :key="item._id"
                         :value="item._id"
                         :label="item.name"></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item label="">
            <el-form-item label=""
                          prop="scores">
              <el-rate style="margin-top: 0.7rem"
                       v-model="herosCreateForm.scores.difficult"
                       :max="9"
                       show-score></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate style="margin-top: 0.7rem"
                       v-model="herosCreateForm.scores.skill"
                       :max="9"
                       show-score></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate style="margin-top: 0.7rem"
                       v-model="herosCreateForm.scores.attack"
                       :max="9"
                       show-score></el-rate>
            </el-form-item>
            <!--  -->
            <el-form-item label="">
              <el-rate style="margin-top: 0.7rem"
                       v-model="herosCreateForm.scores.exist"
                       :max="9"
                       show-score></el-rate>
            </el-form-item>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="goods1">
            <el-select multiple
                       v-model="herosCreateForm.goods1">
              <el-option v-for="item in goods"
                         :key="item._id"
                         :label="item.name"
                         :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="goods2">
            <el-select multiple
                       v-model="herosCreateForm.goods2">
              <el-option v-for="item in goods"
                         :key="item._id"
                         :label="item.name"
                         :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="usage">
            <el-input type="textarea"
                      v-model="herosCreateForm.usage"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="compete">
            <el-input type="textarea"
                      v-model="herosCreateForm.compete"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label=""
                        prop="team">
            <el-input type="textarea"
                      v-model="herosCreateForm.team"></el-input>
          </el-form-item>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label=""
                     name="skill">
          <el-button size="mini"
                     @click="herosCreateForm.skills.push({})"><i class="el-icon-plus"></i> </el-button>
          <el-row>
            <el-col :span="12"
                    v-for="(item, i) in herosCreateForm.skills"
                    :key="i"
                    style="margin-top: 2rem">
              <!--  -->
              <el-form-item label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <!--  -->
                <el-upload class="avatar-uploader"
                           :action="uploadUrl"
                           :headers="setHeaders()"
                           :show-file-list="false"
                           :on-success="
										res => $set(item, 'icon', res.icon)
									">
                  <img v-if="item.icon"
                       :src="item.icon"
                       class="avatar" />
                  <i v-else
                     class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <el-input type="textarea"
                          v-model="item.description"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <el-input type="textarea"
                          v-model="item.tips"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="danger"
                           size="mini"
                           @click="herosCreateForm.skills.splice(i, 1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane label=""
                     name="partners">
          <el-button size="mini"
                     @click="herosCreateForm.partners.push({})"><i class="el-icon-plus"></i> </el-button>
          <el-row>
            <el-col :span="12"
                    v-for="(item, i) in herosCreateForm.partners"
                    :key="i"
                    style="margin-top: 2rem">
              <!--  -->
              <el-form-item label="">
                <el-select v-model="item.hero"
                           placeholder=""
                           filterable>
                  <el-option v-for="hero in heroes"
                             :key="hero._id"
                             :label="hero.name"
                             :value="hero._id">
                  </el-option>
                </el-select>

              </el-form-item>
              <!--  -->
              <el-form-item label="">
                <el-input type="textarea"
                          v-model="item.introduce"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="danger"
                           size="mini"
                           @click="herosCreateForm.partners.splice(i, 1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>

      <!--  -->
      <el-form-item style="margin-top: 1rem">
        <el-button type="primary"
                   @click="submitForm('herosCreateFormRef')"></el-button>
        <el-button @click="resetForm('herosCreateFormRef')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      // 
      herosCreateForm: {
        name: '',
        icon: '',
        banner: '',
        scores: {},
        skills: [],
        partners: []
      },
      // 
      categories: [],
      // 
      goods: [],
      // 
      heroes: [],
      // 
      herosCreateFormRules: {
        name: { required: true, message: '', trriger: 'blur' },
        avatar: {
          required: true,
          message: '',
          trriger: 'blur'
        },
        banner: {
          required: true,
          message: '',
          trriger: 'blur'
        },
        title: { required: true, message: '', trriger: 'blur' },
        usage: { required: true, message: '', trriger: 'blur' },
        compete: {
          required: true,
          message: '',
          trriger: 'blur'
        },
        team: { required: true, message: '', trriger: 'blur' },
        name2: { required: true, message: '', trriger: 'blur' },
        icon: { required: true, message: '', trriger: 'blur' },
        delay: { required: true, message: '', trriger: 'blur' },
        cost: { required: true, message: '', trriger: 'blur' },
        description: {
          required: true,
          message: '',
          trriger: 'blur'
        },
        tips: { required: true, message: '', trriger: 'blur' }
      }
    }
  },
  created() {
    this.id ? this.getHeroById() : ''
    this.getCategories()
    this.getGoods()
    this.getHeroes()
  },
  methods: {
    // 
    submitForm(ref) {
      this.$refs[ref].validate(async valid => {
        // 
        if (!valid) return false
        // 
        if (this.id) {
          // 
          const { data } = await this.$http.put(
            `/reset/heros/${this.id}`,
            this.herosCreateForm
          )
          // 
          if (data.meta.status !== 200)
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        } else {
          // 
          const { data } = await this.$http.post(
            '/reset/heros',
            this.herosCreateForm
          )
          // 
          if (data.meta.status !== 201)
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        }
        //  herosList 
        this.$router.push('/heros/list')
      })
    },
    // 
    resetForm(ref) {
      this.$refs[ref].resetFields()
      this.herosCreateForm.skills = []
    },
    // 
    uploadSuccess(res) {
      this.herosCreateForm.icon = res.icon
    },
    // id
    async getHeroById() {
      const { data } = await this.$http.get(`/reset/heros/${this.id}`)
      this.herosCreateForm = Object.assign(
        {},
        this.herosCreateForm,
        data.message
      )
    },
    // 
    async getCategories() {
      const { data } = await this.$http.get('/reset/categories')
      this.categories = data.message
    },
    // 
    async getGoods() {
      const { data } = await this.$http.get('/reset/goods')
      this.goods = data.message
    },
    // 
    async getHeroes() {
      const { data } = await this.$http.get('/reset/heroes')
      this.heroes = data.message
    }
  },
  watch: {
    //  
    $route() {
      this.herosCreateForm = {}
    }
  }
}
</script>
