<template>
  <div calss="goodsCreate">
    <h1>{{id ? '' : ''}}</h1>
    <el-form :model="goodsCreateForm"
             ref="goodsCreateRef"
             label-width="100px"
             :rules="goodsCreateRules">
      <!--  -->
      <el-form-item label=""
                    prop="name">
        <el-input type="name"
                  v-model.number="goodsCreateForm.name"
                  autocomplete="off"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label=""
                    prop="icon">
        <!--  -->
        <el-upload class="avatar-uploader"
                   :action="uploadUrl"
                   :headers="setHeaders()"
                   :show-file-list="false"
                   :on-success="uploadSuccess">
          <img v-if="goodsCreateForm.icon"
               :src="goodsCreateForm.icon"
               class="avatar">
          <i v-else
             class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm('goodsCreateRef')"></el-button>
        <el-button @click="resetForm('goodsCreateRef')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      // 
      goodsCreateForm: {
        name: '',
        icon: ''
      },
      // 
      goodsCreateRules: {
        name: { required: true, message: '', trigger: 'blur' },
        icon: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  created() {
    // 
    this.id ? this.getGoodsById() : ''
  },
  methods: {
    // 
    submitForm(ref) {
      // 
      this.$refs[ref].validate(async valid => {
        if (!valid) return false
        // 
        if (this.id) {
          // 
          const { data } = await this.$http.put(
            `/reset/goods/${this.id}`,
            this.goodsCreateForm
          )
          if (data.meta.status !== 200) {
            // 
            return this.$message({
              type: 'success',
              message: data.meta.msg
            })
          }
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        } else {
          // 
          const { data } = await this.$http.post(
            '/reset/goods',
            this.goodsCreateForm
          )
          if (data.meta.status !== 201) {
            // 
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          }
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        }

        // 
        this.$refs[ref].resetFields()
        //  goods/list 
        this.$router.push('/goods/list')
      })
    },
    // 
    resetForm(ref) {
      this.$refs[ref].resetFields()
    },
    // 
    async getGoodsById() {
      const { data } = await this.$http.get(`reset/goods/${this.id}`)
      this.goodsCreateForm = data.message
    },
    // 
    uploadSuccess(res) {
      // vue 
      // this.$set(this.goodsCreateForm, 'icon', res.icon)
      this.goodsCreateForm.icon = res.icon
    }
  },
  watch: {
    //  
    $route() {
      this.goodsCreateForm = {}
    }
  }
}
</script>
