<template>
  <div class="categoriesCreate">
    <h1>{{id? '':''}}</h1>
    <el-form :model="createForm"
             ref="createForm"
             label-width="100px"
             :rules="createFormRules">
      <!--  -->
      <el-form-item label="">
        <el-select v-model="createForm.parent">
          <el-option v-for="item in options"
                     :key="item._id"
                     :label="item.name"
                     :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <el-form-item label=""
                    prop="name">
        <el-input type="name"
                  v-model="createForm.name"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm('createForm')"></el-button>
        <el-button @click="resetForm('createForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      // 
      createForm: {},
      // 
      options: {},
      // 
      createFormRules: {
        name: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  created() {
    // 
    this.id ? this.getEditData() : ''
    // 
    this.getCategory()
  },
  methods: {
    // 
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (!valid) return false
        if (this.id) {
          // ---
          const { data } = await this.$http.put(
            `/reset/categories/${this.id}`,
            this.createForm
          )
          if (data.meta.status !== 200) {
            // 
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          }
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        } else {
          // ---
          const { data } = await this.$http.post(
            '/reset/categories',
            this.createForm
          )
          if (data.meta.status !== 201) {
            // 
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          }
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        }

        // 
        this.$refs[formName].resetFields()
        // 
        this.$router.push('/categories/list')
      })
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    // 
    async getEditData() {
      const { data } = await this.$http.get(`/reset/categories/${this.id}`)
      this.createForm = data.message
    },
    // 
    async getCategory() {
      const { data } = await this.$http.get('/reset/categories')
      this.options = data.message
    }
  },
  watch: {
    //  
    $route() {
      this.createForm = {}
    }
  }
}
</script>