<template>
  <div class="articleCreate">
    <h1>{{id ? '' : ''}}</h1>
    <el-form label-width="100px"
             :model="articleCreateForm"
             :rules="articleCreateRules"
             ref="articleCreateRef">
      <!--  -->
      <el-form-item label=""
                    prop="categories">
        <el-select v-model="articleCreateForm.categories"
                   multiple>
          <el-option v-for="item in categories"
                     :key="item._id"
                     :value="item._id"
                     :label="item.name"></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <el-form-item label=""
                    prop="title">
        <el-input v-model="articleCreateForm.title"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label=""
                    prop="body">
        <!--  -->
        <VueEditor v-model="articleCreateForm.body"
                   useCustomImageHandler
                   @image-added="handleImageAdded"></VueEditor>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm('articleCreateRef')"></el-button>
        <el-button @click="resetForm('articleCreateRef')"></el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
import { VueEditor } from 'vue2-editor'

export default {
  props: {
    id: {}
  },
  components: {
    VueEditor
  },
  data() {
    return {
      // 
      articleCreateForm: {},
      // 
      categories: {},
      icon: {},
      // 
      articleCreateRules: {
        categories: {
          required: true,
          message: '',
          trigger: 'blur'
        },
        title: { required: true, message: '', trigger: 'blur' },
        body: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  created() {
    this.getCategories()
    this.id ? this.getArticleData() : ''
  },
  methods: {
    // 
    async getCategories() {
      const { data } = await this.$http.get('/reset/categories')
      this.categories = data.message
    },
    // 
    async submitForm(ref) {
      // 
      this.$refs[ref].validate(async valid => {
        // 
        if (!valid) return false
        // 
        if (this.id) {
          // 
          const { data } = await this.$http.put(
            `/reset/articles/${this.id}`,
            this.articleCreateForm
          )
          if (data.meta.status !== 200) {
            // 
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          }
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        } else {
          // 
          const { data } = await this.$http.post(
            '/reset/articles',
            this.articleCreateForm
          )
          // 
          if (data.meta.status !== 201)
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        }
        //  article/list 
        this.$router.push('/article/list')
      })
    },
    // 
    async resetForm(ref) {
      this.$refs[ref].resetFields()
    },
    // 
    async getArticleData() {
      const { data } = await this.$http.get(`/reset/articles/${this.id}`)
      this.articleCreateForm = data.message
    },
    // 
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)

      const res = await this.$http.post('/upload', formData)
      Editor.insertEmbed(cursorLocation, 'image', res.data.icon)
      resetUploader()
    }
  },
  watch: {
    $route() {
      //  
      this.articleCreateForm = {}
    }
  }
}
</script>