<template>
  <div class="login">
    <el-card class="loginHeader"
             header="">
      <el-form :model="loginForm"
               label-width="4rem"
               :rules="loginFormRules"
               ref="loginFormRef">
        <el-form-item label=""
                      prop="name">
          <el-input v-model="loginForm.name"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="password">
          <el-input type="password"
                    v-model="loginForm.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
                     @click="submitForm('loginFormRef')"></el-button>
          <el-button @click="resetForm('loginFormRef')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {},
      // 
      loginFormRules: {
        name: { required: true, message: '', trigger: 'blur' },
        password: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  methods: {
    // 
    submitForm(ref) {
      // 
      this.$refs[ref].validate(async valid => {
        // 
        if (!valid) return false
        // 
        const { data } = await this.$http.post('/login', this.loginForm)
        // 
        if (data.meta.status !== 200) return false
        //  token
        localStorage.token = data.token
        // 
        this.$router.push('/')
        // 
        this.$message({
          type: 'success',
          message: data.meta.msg
        })
      })
    },
    // 
    resetForm(ref) {
      this.$refs[ref].resetFields()
    }
  }
}
</script>

<style>
.login {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.loginHeader {
  width: 25rem;
  margin: 8rem auto;
}
</style>