<template>
  <div class="adCreate">
    <h1>{{id ? '' : ''}}</h1>
    <el-form :model="adCreateForm"
             :rules="adCreateFormRules"
             ref="adCreateFormRef"
             label-width="100px">
      <!--  -->
      <el-form-item label=""
                    prop="name">
        <el-input v-model="adCreateForm.name"></el-input>
      </el-form-item>
      <el-form-item label=""
                    prop="ad">
        <el-row>
          <!--  -->
          <el-card>
            <el-col :span="24">
              <el-button size="mini"
                         @click="adCreateForm.ad.push({})"><i class="el-icon-plus"></i></el-button>
            </el-col>
            <!--  -->
            <el-col v-for="(item, i) in adCreateForm.ad"
                    :key="i"
                    :span="24"
                    style="margin-top: 1rem;">
              <el-row>
                <!--  -->
                <el-col>
                  <!-- (URL) -->
                  <el-form-item label="(URL)"
                                label-width="120px"
                                prop="url">
                    <el-input v-model="item.url"></el-input>
                  </el-form-item>
                  <!--  -->
                  <el-form-item label=""
                                label-width="120px"
                                prop="icon"
                                style="margin-top: 1rem">
                    <!--  -->
                    <el-upload class="avatar-uploader"
                               :action="uploadUrl"
                               :headers="setHeaders()"
                               :show-file-list="false"
                               :on-success="res =>$set(item, 'img', res.icon)">
                      <img v-if="item.img"
                           :src="item.img"
                           class="avatar">
                      <i v-else
                         class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </el-form-item>
                </el-col>
                <!--  -->
                <el-col>
                  <el-button type="danger"
                             size="mini"
                             style="margin-left: 93%"
                             @click="adCreateForm.ad.splice(i, 1)"></el-button>
                </el-col>
              </el-row>
            </el-col>
          </el-card>
        </el-row>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm('adCreateFormRef')"></el-button>
        <el-button @click="resetForm('adCreateFormRef')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      // 
      adCreateForm: {
        ad: []
      },
      // 
      adCreateFormRules: {
        name: { required: true, message: '', trigger: 'blur' },
        ad: { required: true },
        url: { required: true, message: '', trigger: 'blur' },
        icon: { required: true, message: '', trigger: 'blur' }
      }
    }
  },
  created() {
    this.id ? this.getAdEditData() : ''
  },
  methods: {
    // 
    submitForm(ref) {
      // 
      this.$refs[ref].validate(async valid => {
        // 
        if (!valid) return false
        // 
        if (this.id) {
          // 
          const { data } = await this.$http.put(
            `/reset/ads/${this.id}`,
            this.adCreateForm
          )
          if (data.meta.status !== 200) {
            // 
            return this.$message({
              type: 'error',
              message: data.meta.status
            })
          }
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        } else {
          // 
          const { data } = await this.$http.post(
            '/reset/ads',
            this.adCreateForm
          )
          // 
          if (data.meta.status !== 201)
            return this.$message({
              type: 'error',
              message: data.meta.msg
            })
          // 
          this.$message({
            type: 'success',
            message: data.meta.msg
          })
        }
        //  ad/list 
        this.$router.push('/ad/list')
      })
    },
    // 
    resetForm(ref) {
      this.$refs[ref].resetFields()
    },
    // 
    async getAdEditData() {
      const { data } = await this.$http.get(`/reset/ads/${this.id}`)
      this.adCreateForm = data.message
    }
  }
}
</script>