 <template>
  <div class="goodsList">
    <el-table :data="goodsListData"
              style="width: 100%"
              border
              stripe>
      <!--  -->
      <el-table-column type="index"
                       width="140">
      </el-table-column>
      <!-- ID -->
      <el-table-column label="ID"
                       prop="_id"
                       width="220">
      </el-table-column>
      <!--  -->
      <el-table-column prop="date"
                       label=""
                       width="200">
        <template slot-scope="scope">
          {{scope.row.date | dateFormat}}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="icon"
                       label=""
                       width="100">
        <template slot-scope="scope">
          <img :src="scope.row.icon"
               style="height:3em">
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="name"
                       label="">
      </el-table-column>
      <!--  -->
      <el-table-column label=""
                       width="150">
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     @click="$router.push(`/goods/create/${scope.row._id}`)"></el-button>
          <el-button type="danger"
                     size="mini"
                     @click="removeGoodsById(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

  <script>
export default {
  data() {
    return {
      goodsListData: []
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async removeGoodsById(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        return this.$message({
          type: 'info',
          message: ''
        })
      })
      if (confirm !== 'confirm') return false
      // 
      const { data } = await this.$http.delete(`/reset/goods/${id}`)
      // 
      if (data.meta.status !== 204)
        return this.$message({
          type: 'error',
          message: data.meta.msg
        })
      // 
      this.$message({
        type: 'success',
        message: data.meta.msg
      })
      // 
      this.getGoodsList()
    },
    // 
    async getGoodsList() {
      const { data } = await this.$http.get('/reset/goods')
      this.goodsListData = data.message
    }
  }
}
</script>