<template>
  <div class="categoriesList">
    <el-table :data="categoriesListData"
              style="width: 100%"
              border
              stripe>
      <!--  -->
      <el-table-column type="index"
                       width="140">
      </el-table-column>
      <!-- ID -->
      <el-table-column prop="_id"
                       label="ID"
                       width="220">
      </el-table-column>
      <!--  -->
      <el-table-column label=""
                       width="200">
        <template slot-scope="scope">
          {{scope.row.date | dateFormat}}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="parent.name"
                       label=""> </el-table-column>
      <!--  -->
      <el-table-column prop="name"
                       label=""> </el-table-column>
      <el-table-column label=""
                       width="150">
        <!--  -->
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     @click="$router.push(`/categories/create/${scope.row._id}`)"></el-button>
          <el-button type="danger"
                     size="mini"
                     @click="removeCategoryById(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      categoriesListData: []
    }
  },
  created() {
    this.getListData()
  },
  methods: {
    // 
    async getListData() {
      const { data } = await this.$http.get('/reset/categories')
      this.categoriesListData = data.message
    },
    // 
    async removeCategoryById(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        this.$message({
          type: 'info',
          message: ''
        })
      })
      // 
      if (confirm === 'confirm') {
        const { data } = await this.$http.delete(`/reset/categories/${id}`)
        if (data.meta.status !== 204) {
          // 
          return this.$message({
            type: 'error',
            message: data.meta.msg
          })
        }
        // 
        this.$message({
          type: 'success',
          message: data.meta.msg
        })

        // 
        this.getListData()
      }
    }
  }
}
</script>

<style></style>
