 <template>
  <div class="herosList">
    <el-table :data="herosListData"
              style="width: 100%"
              border
              stripe>
      <!--  -->
      <el-table-column type="index"
                       width="140">
      </el-table-column>
      <!-- ID -->
      <el-table-column prop="_id"
                       label="ID"
                       width="220">
      </el-table-column>
      <!--  -->
      <el-table-column prop="date"
                       label=""
                       width="200">
        <template slot-scope="scope">
          {{scope.row.date | dateFormat}}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="avatar"
                       label=""
                       width="100">
        <template slot-scope="scope">
          <img :src="scope.row.avatar"
               style="height: 3em">
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="title"
                       label="">
      </el-table-column>
      <!--  -->
      <el-table-column prop="name"
                       label="">
      </el-table-column>
      <!--  -->
      <el-table-column label=""
                       width="150">
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     @click="$router.push(`/heros/create/${scope.row._id}`)"></el-button>
          <el-button type="danger"
                     size="mini"
                     @click="removeHero(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

  <script>
export default {
  data() {
    return {
      herosListData: []
    }
  },
  created() {
    this.getHeroList()
  },
  methods: {
    // 
    async getHeroList() {
      const { data } = await this.$http.get('/reset/heros')
      this.herosListData = data.message
    },
    // 
    async removeHero(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        return this.$message({
          type: 'error',
          message: ''
        })
      })
      if (confirm !== 'confirm') return false
      // 
      const { data } = await this.$http.delete(`/reset/heros/${id}`)
      if (data.meta.status !== 204)
        // 
        return this.$message({
          type: 'error',
          message: data.meta.msg
        })
      // 
      this.$message({
        type: 'success',
        message: data.meta.msg
      })
      // 
      this.getHeroList()
    }
  }
}
</script>