import Vue from 'vue'
import VueRouter from 'vue-router'
// 
import Login from '../views/Login.vue'
// 
import Main from '../views/Main.vue'
// 
import CategoriesCreate from '../views/CategoriesCreate.vue'
import CategoriesList from '../views/CategoriesList.vue'
// 
import GoodsCreate from '../views/GoodsCreate.vue'
import GoodsList from '../views/GoodsList.vue'
// 
import HerosCreate from '../views/HerosCreate.vue'
import HerosList from '../views/HerosList.vue'
// 
import ArticleCreate from '../views/ArticleCreate.vue'
import ArticleList from '../views/ArticleList.vue'
// 
import AdCreate from '../views/AdCreate.vue'
import AdList from '../views/AdList.vue'
// 
import AdminCreate from '../views/AdminCreate.vue'
import AdminList from '../views/AdminList.vue'
// 404
import NotFound from '../views/NotFound.vue'

Vue.use(VueRouter)

const routes = [
	// 
	{
		path: '/login',
		name: 'Login',
		component: Login,
		meta: { isPublic: true }
	},
	// 
	{ path: '/', redirect: '/main' },
	{
		path: '/main',
		name: 'Main',
		component: Main,
		children: [
			// 
			{
				path: '/categories/create',
				name: 'CategoriesCreat',
				component: CategoriesCreate
			},
			{
				path: '/categories/create/:id',
				name: 'CategoriesEdit',
				component: CategoriesCreate,
				props: true
			},
			{
				path: '/categories/list',
				name: 'CategoriesList',
				component: CategoriesList
			},
			// 
			{
				path: '/goods/create',
				name: 'GoodsCreate',
				component: GoodsCreate
			},
			{
				path: '/goods/create/:id',
				name: 'GoodsCreate',
				component: GoodsCreate,
				props: true
			},
			{
				path: '/goods/list',
				name: 'GoodsList',
				component: GoodsList
			},
			// 
			{
				path: '/heros/create',
				name: 'HerosCreate',
				component: HerosCreate
			},
			{
				path: '/heros/create/:id',
				name: 'HerosCreate',
				component: HerosCreate,
				props: true
			},
			{
				path: '/heros/list',
				name: 'HerosList',
				component: HerosList
			},
			// 
			{
				path: '/article/create',
				name: 'ArticleCreate',
				component: ArticleCreate
			},
			{
				path: '/article/create/:id',
				name: 'ArticleCreate',
				component: ArticleCreate,
				props: true
			},
			{
				path: '/article/list',
				name: 'ArticleList',
				component: ArticleList
			},
			// 
			{
				path: '/ad/create',
				name: 'AdCreate',
				component: AdCreate
			},
			{
				path: '/ad/create/:id',
				name: 'AdCreate',
				component: AdCreate,
				props: true
			},
			{
				path: '/ad/list',
				name: 'AdList',
				component: AdList
			},
			// 
			{
				path: '/admin/create',
				name: 'AdminCreate',
				component: AdminCreate
			},
			{
				path: '/admin/create/:id',
				name: 'AdminCreate',
				component: AdminCreate,
				props: true
			},
			{
				path: '/admin/list',
				name: 'AdminList',
				component: AdminList
			}
		]
	},
	// 404 
	{ path: '*', name: '404', component: NotFound }
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

// router 
router.beforeEach((to, from, next) => {
	if (!to.meta.isPublic && !localStorage.token) return next('/login')
	next()
})

export default router
