<template>
  <div class="adminList">
    <el-table :data="adminListData"
              style="width: 100%"
              border
              stripe>
      <!--  -->
      <el-table-column type="index"
                       width="140">
      </el-table-column>
      <!-- ID -->
      <el-table-column prop="_id"
                       label="ID"
                       width="220">
      </el-table-column>
      <!--  -->
      <el-table-column prop="date"
                       label=""
                       width="200">
        <template slot-scope="scope">
          {{scope.row.date | dateFormat}}
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="name"
                       label=""
                       width="100">
        <template slot-scope="scope">
          <img :src="scope.row.avatar"
               style="height: 3rem">
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="name"
                       label="">
      </el-table-column>
      <!--  -->
      <el-table-column label=""
                       width="150">
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     @click="$router.push('/admin/create/' + scope.row._id)">
          </el-button>
          <el-button type="danger"
                     size="mini"
                     @click="removeAdminById(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      adminListData: []
    }
  },
  created() {
    this.getAdminListData()
  },
  methods: {
    // 
    async getAdminListData() {
      const { data } = await this.$http.get('/reset/admin')
      this.adminListData = data.message
    },
    // id
    async removeAdminById(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        return this.$message({
          type: 'info',
          message: ''
        })
      })
      if (confirm !== 'confirm') return false
      // 
      const { data } = await this.$http.delete(
        `/reset/admins/${id}`,
        this.adminListData
      )
      if (data.meta.status !== 204)
        // 
        return this.$message({
          type: 'error',
          message: data.meta.msg
        })
      // 
      this.$message({
        type: 'success',
        message: data.meta.msg
      })
      // 
      this.getAdminListData()
    }
  }
}
</script>