<template>
  <div class="adList">
    <template>
      <el-table :data="adListData"
                style="width: 100%"
                border
                stripe>
        <el-table-column type="index"
                         width="140"></el-table-column>
        <el-table-column prop="_id"
                         label="ID"
                         width="220"></el-table-column>
        <el-table-column prop="date"
                         label=""
                         width="200">
          <template slot-scope="scope">
            {{scope.row.date | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column prop="name"
                         label="">
        </el-table-column>
        <el-table-column label=""
                         width="150">
          <template slot-scope="scope">
            <el-button type="primary"
                       size="mini"
                       @click="$router.push(`/ad/create/${scope.row._id}`)"></el-button>
            <el-button type="danger"
                       size="mini"
                       @click="removeAdById(scope.row._id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      adListData: []
    }
  },
  created() {
    this.getAdListData()
  },
  methods: {
    // 
    async getAdListData() {
      const { data } = await this.$http.get('/reset/ads')
      this.adListData = data.message
    },
    // id
    async removeAdById(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        return this.$message({
          type: 'info',
          message: ''
        })
      })
      if (confirm !== 'confirm') return false
      // 
      const { data } = await this.$http.delete(`/reset/ads/${id}`)
      if (data.meta.status !== 204) {
        // 
        return this.$message({
          type: 'error',
          message: data.meta.msg
        })
      }
      // 
      this.$message({
        type: 'success',
        message: data.meta.msg
      })
      // 
      this.getAdListData()
    }
  }
}
</script>