  <template>
  <el-table :data="articleList"
            style="width: 100%"
            border
            stripe>
    <!--  -->
    <el-table-column type="index"
                     width="140"></el-table-column>
    <el-table-column prop="_id"
                     label="ID"
                     width="220"></el-table-column>
    <!--  -->
    <el-table-column label=""
                     width="200">
      <template slot-scope="scope">
        {{scope.row.date | dateFormat}}
      </template>
    </el-table-column>
    <!--  -->
    <el-table-column prop="title"
                     label=""></el-table-column>
    <el-table-column label=""
                     width="150">
      <template slot-scope="scope">
        <!--  -->
        <el-button type="primary"
                   size="mini"
                   @click="$router.push(`/article/create/${scope.row._id}`)"></el-button>
        <el-button type="danger"
                   size="mini"
                   @click="removeArticleById(scope.row._id)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  data() {
    return {
      // 
      articleList: []
    }
  },
  created() {
    this.getArticleList()
  },
  methods: {
    // 
    async getArticleList() {
      const { data } = await this.$http.get('/reset/article')
      this.articleList = data.message
    },
    // id
    async removeArticleById(id) {
      // 
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(() => {
        // 
        return this.$message({
          type: 'info',
          message: ''
        })
      })
      if (confirm !== 'confirm') return false
      // 
      const { data } = await this.$http.delete(`/reset/articles/${id}`)
      // 
      if (data.meta.status !== 204)
        return this.$message({
          type: 'error',
          message: data.meta.msg
        })
      // 
      this.$message({
        type: 'success',
        message: data.meta.msg
      })
      // 
      this.getArticleList()
    }
  }
}
</script>